.images{
    display: flex;
    position: relative;
    width: 100px;
    height:100%;
    -webkit-transform: translate(0,40px);
            transform: translate(0,40px)
    
    
}

.images div img{
    z-index: 20;
}
.image-stack__item--bottom{
    position: absolute;
    transition: all 0.2s ease-in;
}

.image-stack__item--top{
    position: absolute;
    left: 80%;
    top: 30px;   
    transition: all 0.2s ease-in;
}
.image-stack__item--top2{
    position: absolute;
    top: 60px;
    left: 150%;
    transition: all 0.2s ease-in;

}


.image-stack__item--top3{
    position: absolute;
    top: 95px;
    left: 220%;
    transition: all 0.2s ease-in;

}

.image-stack__item--top4{
    position: absolute;
    top: 130px;
    left: 290%;
    transition: all 0.2s ease-in;

}
.image-stack__item--bottom:hover,.image-stack__item--top:hover,.image-stack__item--top2:hover,.image-stack__item--top3:hover,.image-stack__item--top4:hover{
    -webkit-transform: translate(0,-30px);
            transform: translate(0,-30px);
    
    
}

.header-nav{
    -webkit-filter: opacity(0);
    color:white;
    left: 300%;
    font-size: 5em;
    position: absolute;
    z-index: 2;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;

}

.image-stack__item--bottom:hover  .header-nav,.image-stack__item--top:hover  .header-nav,.image-stack__item--top2:hover  .header-nav,.image-stack__item--top3:hover  .header-nav,.image-stack__item--top4:hover .header-nav{
    -webkit-filter: opacity(1);
    -webkit-transform: all 0.5s ease;
    transition: all 0.5s ease;
}

